<template>
    <div class="slide">
        <div class="navimg" @mouseenter="closetime" @mouseleave="starttime">
            <transition-group
            enter-active-class="animate__animated animate__rollIn"
            leave-active-class="animate__animated animate__fadeOut"
            >
                <img v-show="imgshow==index" v-for="(item,index) in imgsrc" :key="index" :src="require('@/assets/img/'+item)">
            </transition-group>
            <div @click="btnprev" class="prev"></div>
            <div @click="btnnext" class="next"></div>
            <div class="bottomNav">
                <div :class="{navC:navChoose==index}" @click="toggleNav(index)" v-for="(item,index) in imgsrc" :key="index"></div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import 'animate.css'
export default {
    data(){
        return{
            imgsrc:[],
            timer:'',
            imgshow:0,
            navChoose:0,
            stopclick:true
        }
    },
    beforeCreate() {
        axios.get('http://www.32wc.cn/music/json/nav.json').then((res)=>{
            this.imgsrc=res.data.imgsrc
        })
        .catch((error)=>{
            console.log(error)
        })
    },
    created() {
        this.runimg()
    },
    methods: {
        runimg:function(){
            this.timer=setInterval(() => {
                if(this.imgshow==this.imgsrc.length-1){
                    this.navChoose=this.imgshow=0;
                }else{
                    this.imgshow++;
                    this.navChoose++
                }
            }, 5000);
        },
        closetime:function(){
            clearInterval(this.timer)
        },
        starttime:function(){
            this.runimg()
        },
        btnprev(){
            if(this.stopclick==true){
                this.stopclick=false;
                if(this.imgshow==0){
                    this.navChoose=this.imgshow=this.imgsrc.length-1
                }else{
                    this.imgshow--;
                    this.navChoose--
                }
                var stoptime=setTimeout(() => {
                    this.stopclick=true;
                    clearTimeout(stoptime)
                }, 1000);
            }
        },
        btnnext(){
            if(this.stopclick==true){
                this.stopclick=false;
                if(this.imgshow==this.imgsrc.length-1){
                    this.navChoose=this.imgshow=0
                }else{
                    this.imgshow++;
                    this.navChoose++
                }
                var stoptime=setTimeout(() => {
                    this.stopclick=true;
                    clearTimeout(stoptime)
                }, 1000);
            }
        },
        toggleNav:function(index){
            if(this.stopclick==true){
                this.stopclick=false;
                this.navChoose=this.imgshow=index
                var stoptime=setTimeout(() => {
                    this.stopclick=true;
                    clearTimeout(stoptime)
                }, 1000);
            }
            
        }
    },
    
        
    
}
</script>
<style lang="less">
.btn{
    width: 45px;
    height: 45px;
    position: absolute;
    top: 50%;
    margin-top: -22.5px;
    cursor: pointer;
}
.slide{
    width: 100%;
    .navimg{
        width: 100%;
        height: 412px;
        max-height: 501px;
        overflow: hidden;
        position: relative;
        img{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
        }
        .prev{
            .btn;
            left: 5px;
            background:url(../assets/img/btnPrev.png);
        }
        .next{
            .btn;
            right: 5px;
            background:url(../assets/img/btnNext.png);
        }
        .bottomNav{
            position: absolute;
            bottom: 20px;
            width: 100%;
            height: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            div{
                width: 15px;
                height: 15px;
                margin:0px 5px;
                border-radius: 50%;
                background: #c3c3c3;
                cursor: pointer;
            }
            div:hover{
                background: #aaa;
            }
            .navC{
                background: #69aaec;
            }
        }
    }
}
</style>