import { createRouter, createWebHashHistory } from 'vue-router'
const musicCom = () => import('@/views/musicCom.vue')
const musicicanCom = () => import('@/views/musicicanCom.vue')
const meCom = () => import('@/views/meCom.vue')
const errorCom = () => import('@/views/errorCom.vue')
const playerCom = () => import('@/views/playerCom.vue')
const albumCom = () => import('@/views/albumCom.vue')
import mainCom from '@/views/mainCom'
import findCom from '@/views/findCom'
const routes = [
  {
    path:'/',
    name:"mainCom",
    component:mainCom,
    children:[
      {
        path:'/',
        name:"findCom",
        components:{
          ban:findCom
        }
      },
      {
        path:'/music/:id',
        name:"musicCom",
        components:{
          ban:musicCom
        }
      },
      {
        path:'/musician/:id',
        name:"musicicanCom",
        components:{
          ban:musicicanCom
        }
      },
      {
        path:'/me',
        name:"meCom",
        components:{
          ban:meCom
        }
      }
    ]
  },
  {
    path:'/:pathMatch(.*)*',
    component:errorCom
  },
  {
    path:'/player',
    name:"playerCom",
    component:playerCom
  },
  {
    path:'/album',
    name:"albumCom",
    component:albumCom
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes,
})

export default router
