<template>
    <div class="find">
        <slideshow-com></slideshow-com>
        <div class="hotword">
            <div class="word">热门话题:</div>
            <div class="wordp">
                <div>
                    <p v-for="(item,index) in hotword" :key="index">{{item.name}}</p>
                </div>
            </div>
            
        </div>
        <!-- 歌单 -->
        <div class="music_song">
            <div class="ren_hd">
                <h3>推荐歌单</h3>
                <div class="ren_style">
                    <div :class="{choosetag:tag==index}" @click="chooseTags(index),toggleimg(item.name)" v-for="(item,index) in musicTags" :key="index">{{item.name}}</div>
                </div>
            </div>
        </div>
        <div class="song_list">
            <div class="list" v-for="(item,index) in imgResult" :key="index">
                <router-link :to="'/music/'+item.id" class="lista">
                    <img class="listimg" :src="item.coverImgUrl">
                </router-link>
                <p>{{item.name}}</p>
            </div>
        </div>

        <!-- 歌手 -->
        <div class="music_song ">
            <div class="ren_hd">
                <h3>热门歌手</h3>
            </div>
        </div>
        <div class="song_list art">
            <div class="list" v-for="(item,index) in artists" :key="index">
                <router-link :to="'/musician/'+item.id" class="lista">
                    <img class="listimg" :src="item.img1v1Url">
                </router-link>
                <p>{{item.name}}</p>
            </div>
        </div>
        <!-- 热门歌单 -->
        <div class="music_song ">
            <div class="ren_hd">
                <h3>热门歌单</h3>
                <div class="hotping">
                    <div>★{{hotresult.name}}★</div>
                </div>
            </div>
            
        </div>
        <div class="song_list art">
            <div class="list" v-for="(item,index) in hotword" :key="index">
                <router-link :to="'/music/'+item.id" class="lista">
                    <img class="listimg" :src="item.picUrl">
                </router-link>
                <!-- <img :src="item.picUrl"> -->
                <p>{{item.name}}</p>
            </div>
        </div>
        <!-- 最新MV -->
        <div class="music_song ">
            <div class="ren_hd">
                <h3>每日推荐MV</h3>
            </div>
        </div>
        <div class="song_list"> 
            <div class="list musicmv" v-for="(item,index) in musicmv" :key="index">
                <div class="mvimg">
                    <img :src="item.cover">
                    <div class="imgstart">
                        <span class="iconfont icon-bofang1"></span>
                    </div>
                </div>
                <p>{{item.name}}</p>
                <div class="mvsp">
                    <span>{{item.artistName}}</span>
                    <span class="iconfont icon-bofang"></span>
                    <span>{{item.playCount}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import slideshowCom from '@/components/slideshowCom.vue'
import axios from 'axios'
export default {
    data(){
        return{
            hotword:[],
            musicTags:[],
            tag:0,
            imgResult:[],
            artists:[],
            hotresult:{name:''},
            musicmv:[]
        }
    },
    methods: {
        useraxios(httpUrl){
            return new Promise((resolve,reject)=>{
                 axios.post(httpUrl).then((res)=>{
                    resolve(res.data)
                })
                .catch((res)=>{
                    console.log(res)
                })
            })
            
        },
        chooseTags:function(index){
            this.tag=index
        },
        toggleimg:function(name){
            this.useraxios(`http://musicapi.leanapp.cn/top/playlist?limit=5&order=hot&cat=${name}`)
            .then((data)=>{
                this.imgResult=data.playlists;
            });
        }
    },
    components:{
        slideshowCom
    },
    beforeMount(){
       this.useraxios('http://musicapi.leanapp.cn/personalized/hot/topic?limit=5')
        .then((data)=>{
            this.hotword=data.result;
            this.hotresult.name=data.result[0].copywriter;
        });
       this.useraxios('http://musicapi.leanapp.cn/playlist/hot')
       .then((data)=>{
           this.musicTags=data.tags;
           this.useraxios(`http://musicapi.leanapp.cn/top/playlist?limit=5&order=hot&cat=${data.tags[0].name}`)
            .then((data)=>{
                this.imgResult=data.playlists;
            });

        });
        this.useraxios('http://musicapi.leanapp.cn/artist/list?type=2&area=96&initial=b&limit=5')
        .then((data)=>{
            this.artists=data.artists
            
        });
        this.useraxios('http://musicapi.leanapp.cn/mv/first?limit=5')
        .then((data)=>{
            this.musicmv=data.data           
        });
        
    },
}
</script>
<style lang="less" scoped>
@mainColor:#00B9F2;
.hotword{
    margin: 40px;
    font-size: 20px;
    font-weight: 600;
    color:@mainColor;
    display: flex;
    .word{
        margin-right: 50px;
        cursor:default;
    }
    .wordp{
        width: 80%;
        height: 26px;
        line-height: 26px;
        font-size: 16px;
        font-weight: normal;
        overflow: hidden;
        div{
            position: relative;
            animation: hot 10s  1s infinite;
        }
    }
}
@keyframes hot {
    0%{
        top: 0px;
    }
    10%{
        top: 0px;
    }
    20%{
        top:-26px;
    }
    30%{
        top: -26px;
    }
    40%{
        top:-52px;
    }
    50%{
        top: -52px;
    }
    60%{
        top: -78px;
    }
    70%{
        top: -78px;
    }
    80%{
        top: -104px;
    }
    90%{
        top: -104px;
    }
    100%{
        top: -130px;
    }
}
.choosetag{
    background: @mainColor;
    color: #FFF !important;
}
.music_song{
    width: 90%;
    margin:20px auto 30px;
}
.ren_hd{
    display: flex;
    h3{
        color: #666;
        font-weight:500;
        margin-right: 50px;
        text-shadow: 0px 0px 2px;
        cursor: pointer;
    }
    .ren_style{
        display: flex;
        justify-content:space-between;
        div{
            width: 72px;
            height: 24px;
            line-height: 24px;
            margin:0px 5px;
            text-align: center;
            color: #333;
            cursor: pointer;
        }
    }
}
.song_list{
    .music_song;
    display: flex;
    .list{
        width: 170px;
        display: flex;
        flex-direction: column;
        color: #666;
        font-size: 14px;
        margin-right: 20px;
        cursor: pointer;
        img{
            display: block;
            width: 170px;
            height: 170px;
        }
        .lista{
            display: block;
            width: 170px;
            height: 170px;
            img{
                display: block;
                width: 170px;
                height: 170px;
            }
        }
        
        p{
            height: 60px;
        }
    }
}
.art{
    text-align: center;
    font-weight: 600;
}
.hotping{
    width: 60%;
    text-align: center;
    color: @mainColor;
    font-weight: 600;
}
.musicmv{
    margin-bottom: 100px;
    p{
        font-weight: 600;
    }
    .mvimg{
        width: 170px;
        height: 170px;
        position: relative;
        overflow: hidden;
        img{
            display: block;
            width: 170px;
            height: 170px;
        }
        .imgstart{
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.4);
            position: absolute;
            top: 0;
            display: none;
            span{
                width: 48px;
                display: block;
                font-size: 48px;
                color: #fff;
                margin: 61px auto 0px;
            }
        }
    }
    .mvimg:hover img{
        transform: scale(1.1);
        transition: all 0.3s;
    }
    .mvimg:hover .imgstart{
        display: block;
    }
    .mvsp{
        span:first-child{
            display: inline-block;
            margin-right: 20px;
        }
    }
}
</style>