<template>
    <div id="nav_con">
        <h1 id="logo">
            <a href="http://www.32wc.cn/">
                <span class="iconfont icon-custom-logo"></span>
                <span class="top">{{navdata.logo}}</span>
            </a>
        </h1>
        <div class="search">
            <el-input
                placeholder="发现音乐..."
                v-model="search">
                <template #prefix>
                <i class="el-input__icon el-icon-search"></i>
                </template>
            </el-input>
        </div>
        <div class="list">
            <router-link class="lista" v-for="(item,index) in navdata.listnav" :key="index" :to="navdata.routername[index]">
                <span class="iconfont" :class="navdata.listicon[index]"></span>
                <span>{{item}}</span>
            </router-link>
        </div>
       <div class="login">
           <button>登录</button>
           <button>注册</button>
       </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    data() {
        return {
            navdata:'',
            search:''
        }
    },
    methods: {
        
    },
    beforeMount(){
        axios.get('http://www.32wc.cn/music/json/nav.json')
        .then((res)=>{
            this.navdata=res.data
        }).catch(res=>{
            console.log(res)
        })
    },
}
</script>

<style lang="less" scoped>
@musicFontColor:#FFF;
a{
    text-decoration: none;
    color: @musicFontColor;
}
.bg{
    background: rgba(0, 0, 0, 0.1);
}
#nav_con{
    width: 90%;
    margin:0px auto;
    overflow: hidden;
    #logo{
        margin-top: 30px;
        a{
            display: flex;
            flex-direction: column;
            text-align: center;
            .iconfont{
                font-size: 46px;
            }
            .top{
                font-size: 32px;
                font-weight: 400;
            }
        }
    }
    .search{
        margin: 10px auto;
    }
    .list{
        .lista{
            height: 40px;
            line-height: 40px;
            display:block;
            transition: all 0.3s;
            span{
                font-size: 18px;
                font-family: "楷体";
            }
            .iconfont{
                margin:0px 10px;
            }

        }
        .lista:hover{
            background: rgba(0, 0, 0, 0.1);
        }
    }
    .login{
        display: flex;
        margin-top: 20px;
        justify-content: space-between;
        button{
            width: 80px;
            height: 30px;
            line-height: 30px;
            font-size: 18px;
            font-family: "楷体";
            color: #409eff;
            background: @musicFontColor;
            border:none;
            border-radius: 2px;
            outline: none;
            cursor: pointer;
            opacity: 0.3;
        }
        button:hover{
            opacity: 1;
        }
    }
}
</style>